import React, { useState, useEffect } from "react";
import styled from 'styled-components';
import { useLocation } from "react-router-dom";
import {
    // createUserWithEmailAndPassword,
    onAuthStateChanged,
    signInWithEmailAndPassword,
    signOut, 
} from "firebase/auth";
import { auth } from "../Firebase";

const LoginBlock = styled.div`
    border-top: 96px solid #ff385c;
    text-align: center; margin-bottom: 200px;
    h3 {text-align: center; padding: 70px 0 10px; font-size: 28px}
    .loginbox { 
        border: 1px solid #555; padding-bottom: 200px;
        min-width: 560px; display: inline-block;
        padding-top: 30px; border-radius: 10px;
        p {
            line-height: 0.3; text-align: left;
            padding-left: 75px;
        }
        input { padding: 10px; min-width: 390px;
                outline: none;
            &:nth-of-type(1) {margin-bottom: 15px}
            }
        button {display: block; padding: 11px;
                min-width: 410px; background: #ff385c;
                margin: 45px auto 0; color: #fff;
                border-radius: 4px; font-size: 16px;
            }
        }
.info {margin-top: 20px; }

`;

const Loginsection = () => {
    // const [registerEmail, setRegisterEmail] = useState("");
    // const [registerPassword, setRegisterPassword] = useState("");
    
    const { pathname } = useLocation();
    useEffect(() => {
        window.scrollTo(0, 0);
    }, [pathname]);

    const [loginEmail, setLoginEmail] = useState(""); 
    const [loginPassword, setLoginPassword] = useState(""); 
    const [user, setUser] = useState({});

    onAuthStateChanged(auth, (currentUser) => {
        setUser(currentUser);
    });

    const login = async () => {
        try {
            const user = await signInWithEmailAndPassword(
                auth,
                loginEmail,
                loginPassword
            );
            console.log(user);
        } catch (error) {
            console.log(error.message);
        }
    };

    //로그아웃
    const logout = async () => {
        await signOut(auth);
    };

    return (
        <LoginBlock>
            <h3>로그인</h3>
            <div className="loginbox">
                <p>이메일</p>
                <input
                    placeholder="Email을 입력하세요. (@를 사용하여주세요.)"
                    onChange={(e) => {
                        setLoginEmail(e.target.value);
                    }}
                />
                <p>비밀번호</p>
                <input
                    placeholder="Password를 입력하세요. (6자리 이상)"
                    onChange={(e) => {
                        setLoginPassword(e.target.value);
                    }}
                />
                <button onClick={login}>로그인</button>
            </div>
            <div className="info">
                {/* <div>User Logged In: {user?.email}</div> */}
                <button onClick={logout}>로그아웃</button>
            </div>
        </LoginBlock>
    );
};

export default Loginsection;