import React, { useState, useEffect } from "react";
import { useLocation } from "react-router-dom";
import { createUserWithEmailAndPassword } from "firebase/auth";
import { auth } from "../Firebase";
import styled from 'styled-components';
import { BiLogInCircle } from "react-icons/bi";
import { Link } from "react-router-dom";

const JoinBlock = styled.div`
    border-top: 96px solid #ff385c;
    text-align: center; margin-bottom: 200px;
    h3 {text-align: center; padding: 70px 0 10px; font-size: 28px}
    .joinbox { 
        border: 1px solid #555; padding-bottom: 200px;
        min-width: 560px; display: inline-block;
        padding-top: 30px; border-radius: 10px;
        p {
            line-height: 0.3; text-align: left;
            padding-left: 75px;
        }
        input { padding: 10px; min-width: 390px;
                outline: none;
            &:nth-of-type(1) {margin-bottom: 15px}
            }
        button {display: block; padding: 11px;
                min-width: 410px; background: #ff385c;
                margin: 45px auto 0; color: #fff;
                border-radius: 4px; font-size: 16px;
            }
        .btnlog {
            margin-left: 275px;
            margin-top: 40px; display: inline-block;
            transition: all 0.3s;
            &:hover {border-bottom: 1px solid #555}
        }
}        
`;

const Joinsection = () => {
    const [registerEmail, setRegisterEmail] = useState("");
    const [registerPassword, setRegisterPassword] = useState("");

    const register = async () => {
        try {
            const user = await createUserWithEmailAndPassword(
                auth,
                registerEmail,
                registerPassword
            );
            console.log(user);
            
        } catch (error) {
            console.log(error.message);
        }
    };

    const { pathname } = useLocation();
    useEffect(() => {
        window.scrollTo(0, 0);
    }, [pathname]);

    return (
        <JoinBlock>
            <h3>에어비앤비에 오신 것을 환영합니다.</h3>
            <div className="joinbox">
                <p>이메일</p>
                <input placeholder="Email을 입력하세요. (@를 사용하여주세요.)" onChange={(e) => {
                        setRegisterEmail(e.target.value);
                        }} />
                <p>비밀번호</p>
                <input placeholder="Password를 입력하세요. (6자리 이상)" type="password" onChange={(e) => {
                        setRegisterPassword(e.target.value);
                        }} />
                <button onClick={register}>회원가입</button>
                <Link to="/login" className="btnlog"><BiLogInCircle  style={{verticalAlign:'middle', paddingRight:'5px'}} />로그인 하러 가기</Link>
           </div>
        </JoinBlock>
    );
};

export default Joinsection;