import React, { useEffect } from 'react';
import Header from '../components/Header';
import Footer from '../components/Footer';
import styled from 'styled-components';
import { useLocation } from 'react-router-dom';

const NewsDetailBlock = styled.div`
    border-top: 96px solid #ff385c;
    padding: 70px 20px 150px;
    h2 {font-size: 26px; font-weight: 700; padding-right: 30px }
    .ndate {font-size: 14px; color: #777; padding-bottom: 20px; 
            line-height: 0.1; }
    .nimg {width: 80%; padding-bottom: 20px}
    .ncont {line-height: 1.6; font-size: 17px; padding-right: 40px}
`;

const NewsDetail = () => {

    const location = useLocation();
    console.log(location.state);

    const { pathname } = useLocation();
    useEffect(() => {
        window.scrollTo(0, 0);
    }, [pathname]);

    return (
        <div>
            <Header />
            <NewsDetailBlock>
                <div className='row'>
                    <h2>{location.state.title}</h2>
                    <p className='ndate'>{location.state.publishedAt.slice(0,10)}</p>
                    <p className='nimg'>
                        <img src={location.state.urlToImage} alt={location.state.title} />
                    </p>
                    <p className='ncont'>{location.state.description}</p>
                </div>
            </NewsDetailBlock>
            <Footer />
        </div>
    );
};

export default NewsDetail;